.YearExpenses {
  position: relative;
  /* margin-bottom: 1px; */
  padding: 0 20px 15px;
  text-align: center;
  /* margin: 20px; */
  background-color: #f1f1f1;
  /* border-bottom: 1px dashed #d4d4d4; */
  /* border: none; */
  /* border-radius: 5px; */
}

.Wrapper {
  display: flex;
  justify-content: space-between;
  height: 57px;
}

.ListWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  width: 7%;
}

.ListWrapper:hover {
  cursor: pointer;
}

.ListWrapper p {
  font-size: 12px;
  text-align: center;
  margin-top: 3px;
}

.List {
  position: relative;
  height: 100%;
  width: 100%;
  border-bottom: 1px solid black;
}

.Expenses {
  position: absolute;
  bottom: 22px;
  left: 0;
  width: 100%;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0) 49.9%, #2e2e2e 49.9%, #2e2e2e 60%, rgba(0, 0, 0, 0) 60%) fixed,linear-gradient(135deg, #2e2e2e 10%, rgba(0, 0, 0, 0) 10%) fixed;
  background-size: 0.3em 0.3em;
  z-index: 1;
}

.Incomes {
  background-color: #d9d9d9;
/* z-index: 1; */
  opacity: .9;
}

.Active div:after {
  content: "";
  position: absolute;
  bottom: -3px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #000;
  /* opacity: .7; */
  /* border-bottom: 3px solid black; */
}

.Active ~ p {
  /* color: #ff6060; */
  font-weight: 700;
}

.LeftBtn, .RightBtn {
  position: absolute;
  top: -55px;

  width: 40px;
  height: 40px;
  background-color: transparent;
  border-color: transparent;
  border-radius: 50%;

  cursor: pointer;
}

.LeftBtn:active, .RightBtn:active {
  outline: none;
  box-shadow: 1px 1px 10px rgb(0 0 0 / 30%);
}

.LeftBtn {
  left: 40px;
}

.RightBtn {
  right: 40px;
}

.LeftBtn::after, .LeftBtn::before,
.RightBtn::after, .RightBtn::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 3px;
  background-color: #646464;
}

.LeftBtn::after, .LeftBtn::before {
  left: 6px;
}

.RightBtn::after, .RightBtn::before {
  right: 6px;
}

.LeftBtn::after, .RightBtn::after {
  top: 10px;
}

.LeftBtn::before, .RightBtn::before {
  top: 23px;
}

.LeftBtn::after, .RightBtn::before {
  transform: rotate(-45deg);
}

.LeftBtn::before, .RightBtn::after {
  transform: rotate(45deg);
}

@media (max-width: 310px) {
  .ListWrapper p {
    font-size: 10px;
  }
}

@media (max-width: 330px) {
  .LeftBtn {
    left: 20px;
  }

  .RightBtn {
    right: 20px;
  }
}
